import { useLocalStorage } from 'hooks/useLocalStorage'
import { useHistory } from 'react-router-dom'
import { useModal } from '../../../hooks/useModal'
import Modal from '../../Modal/Modal'
import classes from './Price.module.css'

export default function Price({ id, discount, basePrice, buyGame }) {
  const { storedValue } = useLocalStorage('esaUser')
  const { push } = useHistory()
  const { currentModal, activeModal, onClose, showCurrentModal } =
    useModal('login')

  const onSubmitHandler = async () => {
    if (!storedValue?.token) {
      showCurrentModal()
      return
    }

    if (!id) return

    const response = await buyGame({ id, token: storedValue?.token })

    if (!response) return

    push(response.links.href)
  }

  return (
    <section className={classes['container']}>
      {activeModal && <Modal onClose={onClose}>{currentModal}</Modal>}

      <article className={classes['price']}>
        <div className={classes['discount']}>
          <p>Discount</p>
          <span>{discount * 100}%</span>
        </div>

        <div className={classes['real-price']}>
          <h3>Digital price</h3>

          <p>This price includes the percentage discount</p>

          <span>${(basePrice * (1 - discount)).toFixed(2) * 1}</span>

          <button onClick={onSubmitHandler} className={classes['btn']}>
            buy digital
          </button>
        </div>
      </article>
    </section>
  )
}
