import { useEffect, useState } from 'react'
import { useHistory, useLocation as location } from 'react-router-dom'

export function useLocation(initialKeyword = '') {
  const [param, setParams] = useState('')
  const [keyword, setKeyword] = useState(initialKeyword)
  const [loading, setLoading] = useState(true)
  const history = useHistory()
  const url = location()

  const getParam = () => {
    setLoading(true)
    const params = new URLSearchParams(url.search).get(keyword)
    setParams(params || '')
    setLoading(false)
    history.listen((route) => {
      console.log('change location')
    })
  }

  useEffect(getParam, [keyword, url])

  return { value: param, loading }
}
