import { Helmet } from 'react-helmet'
import { Redirect, useParams } from 'react-router-dom'

import PrincipalGame from 'components/Game/Game'
import Price from 'components/Game/Price/Price'
import Recommendations from 'components/Game/Recommendations/Recommendations'

import { useGame } from 'hooks/useGame'
import classes from './Game.module.css'

export default function Game() {
  const { id } = useParams()
  const { game, loading, searchById, error, buyGame } = useGame(id)

  if (error) return <Redirect to="/game-not-found" />

  return (
    <>
      <Helmet>
        <title> {game['title'] || ''} | Game | Essteam </title>
      </Helmet>

      {loading ? (
        <p className={classes['loader']}>Loading...</p>
      ) : (
        <>
          <PrincipalGame game={game} />

          <Price
            id={id}
            discount={game['discount']}
            basePrice={game['basePrice']}
            buyGame={buyGame}
          />

          <Recommendations
            title={game['title']}
            id={id}
            searchById={searchById}
          />
        </>
      )}
    </>
  )
}
