import Button from './Button/Button'
import Header from './Header/Header'
import Numbers from './Numbers/Numbers'
import Row from './Row/Row'

import classes from './Reports.module.css'

export default function Reports(props) {
  const {
    payments = [],
    loading,
    authorized,
    count = 0,
    update,
    current
  } = props

  if (!authorized) return <></>

  if (loading)
    return (
      <div className={`${classes['response']}`}>
        <h2 className={`${classes['result']}`}>Loading...</h2>
      </div>
    )

  if (!payments.length)
    return (
      <div className={`${classes['response']}`}>
        <h2 className={`${classes['result']}`}>No payments...</h2>
      </div>
    )

  return (
    <main className={classes['container']}>
      <h1 className={classes['title']}>Reports</h1>

      <section className={classes['table']}>
        <Header />
        {payments.map((payment) => (
          <Row key={payment.id} {...payment} />
        ))}
      </section>

      <div className={classes['pagination']}>
        {current !== 0 && (
          <Button name="prev" onClick={() => update(current - 1)} />
        )}
        <Numbers update={update} count={count} active={current} />
        {current + 1 !== count && (
          <Button name="next" onClick={() => update(current + 1)} />
        )}
      </div>
    </main>
  )
}
