import { useContext } from 'react'
import { Redirect } from 'react-router-dom'

import UserContext from 'context/userContext'

import Backward from 'components/Backward/Backward'
import Reports from 'components/Reports/Reports'
import Modal from 'components/Modal/Modal'

import { useModal } from 'hooks/useModal'
import { useReports } from 'hooks/useReports'

import Denied from '../Denied/Denied'
import classes from './Dashboard.module.css'
import Button from './NewGame/NewGame'
import { Url } from 'utils/url.params'

export default function Dashboard() {
  const { user } = useContext(UserContext)
  const createModal = useModal('create')
  const initialOffset = Url('offset')
  const { reports, loading, current, update } = useReports(initialOffset)

  if (!user) return <Redirect to="/" />

  if (!user['isAuthorized']) return <Denied />

  return (
    <>
      {!reports.authorized && <Redirect to="/unauthorized" />}

      {createModal.activeModal && (
        <Modal onClose={createModal.onClose}>{createModal.currentModal}</Modal>
      )}

      <main className={classes['container']}>
        <Backward />

        <Reports
          loading={loading}
          authorized={reports.authorized}
          payments={reports.results}
          count={reports.count}
          current={current}
          update={update}
        />

        <Button onClick={createModal.showCurrentModal} />
      </main>
    </>
  )
}
